.container{
    width: 100%;
    height: 400px;
    display: flex;
    flex-direction: column;
}

h2{
    font-family:  'Lucida Sans Regular', 'Lucida Sans', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    color: #303F9F;
}

.titulo{
    width: 100%;
    height: 100px;
    text-align: center;
    justify-content: center;
}

.div-class-servico{
    height: auto;
    width: 100%;
}

.servico-1{
    grid-area: servico1;
}

.imagem span{
    display:none;
}
.imagem:hover span{
     display:block;
}
.imagem:hover img{
    display:none;
}
.class-servico{
    height: auto;
    width: 40%;
    display: grid;
    margin: 0 auto;
    gap: 10%;
    grid-template-areas: 
        "servico1 . ";
    grid-template-columns: 320px 320px;
    grid-template-rows: 380px;
}

.class-servico > div{
    height: 380px;
    width: 320px;
    border-width: thin;
    border-style: solid;
    border-color: rgba(102, 90, 90, 0);
}

img{
    width: 250px;
    height: 250px;
    align-items: center;
    margin: 10px;
}

.texto{
    width: 320px;
    height: 30px;
    font-size: 20px;
    text-align: center;
    justify-content: center;
    font-family:  'Lucida Sans Regular', 'Lucida Sans', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}